@import "../../../../styles/helpers";

.title {
    margin-bottom: 32px; }

.line {
    display: flex;
    margin-bottom: 32px;
    strong {
        white-space: nowrap;
        font-weight: 500; } }

.icon {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-shrink: 0;
    width: 48px;
    height: 48px;
    margin-right: 16px;
    background: $purple;
    border: 2px solid $purple;
    border-radius: 50%;
    transition: all .2s;
    svg {
        fill: $neutrals8;
        transition: fill .2s; }
    .loader {
        position: absolute;
        top: -2px;
        left: -2px;
        right: -2px;
        bottom: -2px; } }

.list {
    display: flex;
    flex-wrap: wrap;
    margin: -32px -16px 0;
    @include x {
        margin: -32px -10px 0; }
    @include d {
        margin: -32px -16px 0; }
    @include m {
        display: block;
        margin: 0; } }

.card {
    //flex: 0 0 calc(13.333% - 32px)
    //width: calc(13.333% - 32px)
    //margin: 32px 16px 0
    //+x
    //    flex: 0 0 calc(13.333% - 20px)
    //    width: calc(13.333% - 20px)
    //    margin: 32px 10px 0
    //+d
    //    flex: 0 0 calc(20% - 32px)
    //    width: calc(20% - 32px)
    //    margin: 32px 16px 0
    //+m
    //    width: 30%
    //    margin: 0
    //    &:not(:last-child)
    //        margin-bottom: 32px
    max-width: 40%; }

.details {
    flex-grow: 1; }

.info {
    @include body-bold-2; }

.text {
    color: $neutrals4; }

.switch {
    flex-shrink: 0;
    margin-left: 16px; }

.head {
    display: flex;
    align-items: center; }

.row {
    display: flex;
    padding-top: 12px;
    &:first-child {
        padding: 0 0 12px;
        border-bottom: 1px solid $neutrals6;
        font-weight: 500;
        @include dark {
            border-color: $neutrals3; } } }

.title {
    margin-bottom: 8px;
    left: 22%;
    transform: translateX(22%); }

.col {
    @include body-2;
    &:first-child {
        color: $neutrals4; }
    &:nth-child(2) {
        margin-left: auto;
        padding-left: 30px;
        font-weight: 500; } }

.btns {
    margin-top: 32px; }

.button {
    width: 100%;
    &:not(:last-child) {
        margin-bottom: 8px; } }
